
// imports
import { mapState, mapGetters } from 'vuex'
// import lightGallery from 'lightgallery.js'
// import 'lightgallery.js/dist/css/lightgallery.css'

export default {
  head: {
    title: 'Species View'
  },

  auth: false,
  layout: 'landing',

  data: () => ({
    bcrumbs: [
      {
        text: 'Home',
        to: '/',
      }, {
        text: 'Species',
        to: '/species',
        exact: true,
      }, {
        text: 'View',
        to: null,
        active: true
      }
    ],
    componentLoading: false,
    selectedImage: "",
    dialog: false
  }),

  computed: {
    ...mapGetters({
      specie: 'species/getSpecie'
    }),

    vuetifyBreakpoint () {
      return this.$vuetify.breakpoint.name
    }
  },

  watch: {
    vuetifyBreakpoint (newVal, oldVal) {
      console.log(oldVal)
      console.log(newVal)

      // re-initialize lightgallery
      this.initializeLightGallery()
    }
  },

  mounted () {
    // initialize lightgallery
    setTimeout(() => {
      this.initializeLightGallery()
    }, 1000)

    this.initialize()
  },

  methods: {
    initializeLightGallery () {
      // lightgallery
      let el = document.getElementById('speciesGallery')
      window.lightGallery(el, {
        mode: 'lg-fade',
        thumbnail: true,
        exThumbImage: 'data-src',
        zoom: true,
        rotate: true
      })

      let el2 = document.getElementById('speciesDisplayPhoto')
      window.lightGallery(el2, {
        width: `${this.$vuetify.breakpoint.sm || this.$vuetify.breakpoint.xs ? '95%' : '50%'}`,
        height: '500px',
        mode: 'lg-fade',
        addClass: 'speciesDisplayPhoto',
        counter: false,
        download: false,
        startClass: '',
        enableSwipe: false,
        enableDrag: false,
        speed: 500,
        thumbnail: false,
        exThumbImage: 'data-src',
        zoom: false,
        rotate: false,
        zoomOrigin: false
      })
    },

    async initialize () {
      await this.$store.dispatch('species/fetchSpecie', { id: this.$route.params.id })
    },

    redirectBack () {
      const queryString = window.location.search
      const urlParams = new URLSearchParams(queryString)

      this.$router.push(`/species?${urlParams}`)
    },

    searchSpecies() {
      this.$router.push('/species?keyword='+this.keyword)
    },

    openImage(url) {
      console.log("Zoom", url);
      this.selectedImage = url;
      this.dialog = true;
    }
  }
}

